<template>
  <div>
    <main id="main">
      <!-- ======= Services Section ======= -->
      <section id="services" class="services">
        <div class="container">
          <div class="section-title" data-aos="fade-in" data-aos-delay="100">
            <h2>부동산 서비스</h2>
            <p>
              Magnam dolores commodi suscipit. Necessitatibus eius consequatur ex aliquid fuga eum
              quidem. Sit sint consectetur velit. Quisquam quos quisquam cupiditate. Et nemo qui
              impedit suscipit alias ea. Quia fugiat sit in iste officiis commodi quidem hic quas.
            </p>
          </div>

            <div class="input-group mb-3">
            
            <input v-model="$store.state.board.searchWord" @keydown.enter="boardList" type="text" class="form-control">
            <button @click="boardList" class="btn btn-success" type="button" >Search</button>
          </div>

            <table class="table table-hover">
              <thead>
                <tr>
                  <th>no</th>
                  <th>아파트 명</th>
                  <th>거래금액</th>
                  <th>건축년도</th>
                  <th>전용면적</th>
                  <th>법정동</th>
                  <th>지번</th>
                  <th>거래 타입</th>
                </tr>
              </thead>
                <tbody>
                  <!-- store 사용 -->

                  <!-- 직접 store 에 접근해도 된다. -->
                  <!-- <tr v-for="(board, index) in $store.state.board.list" @click="boardDetail(board.boardId)" v-bind:key="index"> -->
                  
                  <!-- getters 를 이용하는 코드 -->
                  <!-- computed - listGetters - getBoardList  -->
                  <tr v-for="(board, index) in listGetters" @click="boardDetail(board.boardId)" v-bind:key="index">
                    <td>{{ board.no }}</td>
                    <td>{{ board.aptName }}</td>
                    <td>{{ board.dealAmount }}</td>
                    <td>{{ board.area }}</td>
                    <td>{{ board.dong }}</td>
                    <td>{{ board.jibun }}</td>
                    <td>{{ board.dealType }}</td>
                  </tr>
                </tbody>
              </table>
          <pagination></pagination>
          <detail-modal></detail-modal>
        </div>
      </section>
    </main>
  </div>
</template>

<script>
import Pagination from "./Pagination.vue";
import DetailModal from "../modals/DetailModal.vue";

import { Modal } from 'bootstrap';

import http from "@/common/axios.js";
//import util from "@/common/util.js";

export default {
  name: "HouseBoard",
  components: {
    DetailModal,
    Pagination,
  },
  data() {
    return {
      detailModal : null,
      showModal: false,
    };
  },
  computed :{
    // gttters 이용
    listGetters(){
      return this.$store.getters.getBoardList; // no getBoardList()
    }
  },
  methods : {
    boardList(){
      this.$store.dispatch('boardList');      
    },
    movePage(pageIndex){
      console.log("BoardMainVue : movePage : pageIndex : " + pageIndex );

      // store commit 으로 변경
      // this.offset = (pageIndex - 1) * this.listRowCount;
      // this.currentPageIndex = pageIndex;
      this.$store.commit( 'SET_BOARD_MOVE_PAGE', pageIndex );


      this.boardList();
    },

    boardDetail(aptName){

      http.get(
      '/boards/' + aptName, // props variable
      )
      .then(({ data }) => {
        console.log("DetailModalVue: data : ");
        console.log(data);
        
        if( data.result == 'login' ){
          this.$router.push("/login")
        }else{
          this.$store.commit(
            'SET_BOARD_DETAIL',
            { 
              no: data.dto.no,
              aptName: data.dto.aptName,
              dealAmount: data.dto.dealAmount,
              area: data.dto.area,
              dong: data.dto.dong,
              jibun: data.dto.jibun,
              dealType: data.dto.dealType

            }
          );

          this.detailModal.show();
        }
      })
      .catch((error) => {
        console.log("DetailModalVue: error ");
        console.log(error);
      });

      
    },


  },
  created() {
    this.boardList();
  },
  mounted() {
    this.detailModal = new Modal(document.getElementById('detailModal'));
  },

};
</script>

<style scoped>
</style>
